<template>
    <div>
        <nav>
            <div class="nav-wrapper grey lighten-2 ">
                <div class="container">
                    <ul>
                        <li><router-link to="/" class="black-text left brand-logo" >Playscapes</router-link></li>
                    </ul>
                    
                    <ul class="right">
                        <li v-if="isLoggedIn"><router-link to="/" class="black-text">{{currentUser}}</router-link></li>
                        <li v-if="!isLoggedIn"><router-link to="/login" class="black-text">Login</router-link></li>
                        <li v-if="!isLoggedIn"><router-link to="/register" class="black-text">Register</router-link></li>
                        <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">Logout</button></li>
                    </ul>
                </div>
            </div>
        </nav>
    </div>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if (firebase.auth().currentUser) {
            this.isLoggedIn = true
            this.currentUser = firebase.auth().currentUser.email
        }
    },
    methods: {
        logout: function() {
            firebase.auth().signOut().then(
                () => {
                    this.$router.go({path: this.$router.path})
                }
            )
        }
    }
}
</script>


